    background-color:red !important;
    color: white;
    font-weight: bold;
}
#high{
    background-color:#07d507 !important;
    color: black;
    font-weight: bold;
}
#labelDiv{
    display: none;
    flex-direction: column;
}
#trendPrediction{
    display: flex;
    justify-content: center;
}
@media (max-width: 1000px){
    #trendLabel{
        margin-right: 0rem;
        align-items: flex-start;
        text-align: left;
    }
}
@media (max-width: 765px){
    tbody>tr:nth-of-type(even){
        background-color: rgba(184, 184, 255, 0.5);
    }
    tbody>tr:nth-of-type(odd){
        background-color: #f3fffe;
    }
    #trend{
        flex-direction: column;
    }
    #trendSelect{
        width: 80%;
        margin: 1rem;
    }
}
@media (max-width: 610px){
    #trendSelect{
        width: 100%;
    }
    #trendForm{
        flex-direction: column;
    }
}
